(function() {
  this.App.component('mnoeDashboardTemplatesList', {
    templateUrl: 'app/components/mnoe-dashboard-templates-list/mnoe-dashboard-templates-list.html',
    controller: ["$uibModal", "toastr", "MnoeDashboardTemplates", "MnoeAdminConfig", function($uibModal, toastr, MnoeDashboardTemplates, MnoeAdminConfig) {
      var fetchDashboardTemplates, updateSearch, updateSort, vm;
      vm = this;
      vm.datesFormat = MnoeAdminConfig.dashboardTemplatesDatesFormat();
      vm.dashboardTemplates = {
        search: {},
        sort: "name",
        nbItems: 10,
        page: 1,
        pageChangedCb: function(nbItems, page) {
          var offset;
          vm.dashboardTemplates.nbItems = nbItems;
          vm.dashboardTemplates.page = page;
          offset = (page - 1) * nbItems;
          return fetchDashboardTemplates(nbItems, offset);
        }
      };
      vm.callServer = function(tableState) {
        var search, sort;
        sort = updateSort(tableState.sort);
        search = updateSearch(tableState.search);
        return fetchDashboardTemplates(vm.dashboardTemplates.nbItems, vm.dashboardTemplates.offset, sort, search);
      };
      updateSort = function(sortState) {
        var sort;
        if (sortState == null) {
          sortState = {};
        }
        sort = "name";
        if (sortState.predicate) {
          sort = sortState.predicate;
          if (sortState.reverse) {
            sort += ".desc";
          } else {
            sort += ".asc";
          }
        }
        return vm.dashboardTemplates.sort = sort;
      };
      updateSearch = function(searchingState) {
        var attr, ref, search, value;
        if (searchingState == null) {
          searchingState = {};
        }
        search = {};
        if (searchingState.predicateObject) {
          ref = searchingState.predicateObject;
          for (attr in ref) {
            value = ref[attr];
            search['where[' + attr + '.like]'] = value + '%';
          }
        }
        return vm.dashboardTemplates.search = search;
      };
      vm.openDeleteModal = function(dashboardTemplateId) {
        var modalInstance;
        modalInstance = $uibModal.open({
          templateUrl: 'app/views/dashboard-templates/modals/delete-dashboard-template-modal.html',
          controller: 'deleteDashboardTemplateCtrl',
          size: 'lg',
          resolve: {
            dashboardTemplateId: dashboardTemplateId
          }
        });
        return modalInstance.result.then(function(result) {
          if (result) {
            return fetchDashboardTemplates();
          }
        });
      };
      fetchDashboardTemplates = function(limit, offset, sort, search) {
        if (sort == null) {
          sort = vm.dashboardTemplates.sort;
        }
        if (search == null) {
          search = vm.dashboardTemplates.search;
        }
        vm.dashboardTemplates.loading = true;
        return MnoeDashboardTemplates.templates(limit, offset, sort, search).then(function(response) {
          vm.dashboardTemplates.totalItems = response.headers('x-total-count');
          return vm.dashboardTemplates.list = response.data;
        })["finally"](function() {
          return vm.dashboardTemplates.loading = false;
        });
      };
      vm.toggleTemplatePublished = function(id) {
        return MnoeDashboardTemplates.toggleTemplatePublished(id);
      };
    }]
  });

}).call(this);

