        scope.organizations = {
          displayList: [],
          widgetTitle: 'mnoe_admin_panel.dashboard.organization.widget.local_list.loading_orgs.title',
          search: ''
        };
        setAllOrganizationsList = function() {
          scope.organizations.widgetTitle = 'mnoe_admin_panel.dashboard.organization.widget.local_list.all_organizations.title';
          scope.organizations.switchLinkTitle = 'mnoe_admin_panel.dashboard.organization.widget.local_list.all_organizations.switch_link_title';
          return scope.organizations.displayList = $filter('orderBy')(scope.list, 'email');
        };
        setLastOrganizationsList = function() {
          scope.organizations.widgetTitle = 'mnoe_admin_panel.dashboard.organization.widget.local_list.last_organizations.title';
          scope.organizations.switchLinkTitle = 'mnoe_admin_panel.dashboard.organization.widget.local_list.last_organizations.switch_link_title';
          scope.organizations.displayList = $filter('orderBy')(scope.list, '-created_at');
          return scope.organizations.displayList = $filter('limitTo')(scope.organizations.displayList, 10);
        };
        setSearchOrganizationsList = function() {
          var searchToLowerCase;
          scope.organizations.widgetTitle = 'mnoe_admin_panel.dashboard.organization.widget.local_list.search_organizations.title';
          delete scope.organizations.switchLinkTitle;
          searchToLowerCase = scope.organizations.search.toLowerCase();
          scope.organizations.displayList = _.filter(scope.list, function(user) {
            var email, name, surname;
            if (user.email) {
              email = _.contains(user.email.toLowerCase(), searchToLowerCase);
            }
            if (user.name) {
              name = _.contains(user.name.toLowerCase(), searchToLowerCase);
            }
            if (user.surname) {
              surname = _.contains(user.surname.toLowerCase(), searchToLowerCase);
            }
            return email || name || surname;
          });
          return scope.organizations.displayList = $filter('orderBy')(scope.organizations.displayList, 'email');
        };
        displayNormalState = function() {
          if (attrs.view === 'all') {
            return setAllOrganizationsList();
          } else if (attrs.view === 'last') {
            return setLastOrganizationsList();
          } else {
            return $log.error('Value of attribute view can only be "all" or "last"');
          }
        };
        scope.switchState = function() {
          if (attrs.view === 'all') {
            attrs.view = 'last';
          } else {
            attrs.view = 'all';
          }
          return displayNormalState();
        };
        scope.searchChange = function() {
          if (scope.organizations.search === '') {
            return displayNormalState();
          } else {
            return setSearchOrganizationsList();
          }
        };
