$templateCache.put('app/views/product-markups/markup-info-modal/markup-info.html','<div id="markup-info">\n  <div class="modal-header">\n    <h3 class="modal-title" translate>mnoe_admin_panel.dashboard.product_markups.markup_info.modal.title</h3>\n  </div>\n  <div class="modal-body">\n    <p>Prices are managed through markups: A markup is a monetary amount added to the existing price of a product to calculate what price is shown to a customer.</p>\n    <p>\n      A markup can be added through the following scenarios:\n      - The entire application marketplace - All products and customers\n      - An individual customer - Any product for a single customer\n      - An individual product: A single product for all customers</p>\n    <p>- A combination of product and customers: A specific customer purchasing a specific product\n\n      A markup is never placed atop another markup, to avoid complex pricing.\n\n      Markups are applied as follows:\u2028</p>\n    <p>\n      <b>1. A markup applied to specific products and customers take first priority:</b>\n      When a markup is applied to a specific product and customer, activated during purchase, all other markups impacting the specified product will be ignored. (Case 2, 3 &amp; 4)</p>\n    <p>\n      <b>2. Markups applied to a specific customer are second in priority</b>\n      When no markup is specified for a customer + product (As in Case 1), but a markup is specified for a particular customer, it will be applied to all purchases by this customer. Any other markups impacting the customer or product will be ignored (Case 3 &amp; 4).</p>\n    <p>\n      <b>3. Markups applied to a specific product are third in priority</b></p>\n    <p>When a markup is specified for a particular product, it will be applied for any customers purchasing it who don\u2019t already have a customer specific markup (Case 1 &amp; 2). General markups, as shown in case 4, will be ignored.</p>\n    <p>\n      <b>4. General markups have the lowest priority</b>\n      When no markups are specified on a customer or product level, any markups attached to the entire marketplace will be applied to customers purchasing any product.\n    </p>\n  </div>\n  <div class="modal-footer">\n    <div class="row actions">\n      <div class="col-xs-12">\n        <a href="" ng-click="vm.onCancel()"><i class="fa fa-times"></i>{{ \'mnoe_admin_panel.dashboard.product_markups.add_markup.modal.close\' | translate}}</a>\n      </div>\n    </div>\n  </div>\n\n</div>\n');
